
import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import Home from './pages/Home';
import Topics from './pages/Topics';
import People from './pages/People';
import { createBrowserHistory } from "history";
import Analytics from "@segment/analytics.js-core/build/analytics";
import SegmentIntegration from "@segment/analytics.js-integration-segmentio";

// instantiate the library
const analytics = new Analytics();

// add Segment's own integration ( or any other device mode integration ) 
analytics.use(SegmentIntegration);

// define the integration settings object. 
// Since we are using only Segment integration in this example, we only have 
// "Segment.io" in the integrationSettings object
const integrationSettings = {
  "Segment.io": {
    apiKey: "WFt7mza4WqavKAQl7IHI339FBRHGJtE0",
    retryQueue: true,
    addBundledMetadata: true
  }
};

// Initialize the library
analytics.initialize(integrationSettings);
// Happy tracking! 

function App() {

  return (
    <div className="App">
      <div>
        <Router>
        <nav>
          <ul>
            <li>
              <Link to="/segment-test-app/">Home</Link>
            </li>
            <li>
              <Link to="/segment-test-app/people">People</Link>
            </li>
            <li>
              <Link to="/segment-test-app/topics">Topics</Link>
            </li>
          </ul>
        </nav>
        <Switch>
          <Route path="/segment-test-app/people">
            <People analytics={analytics}/>
          </Route>
          <Route path="/segment-test-app/topics">
            <Topics analytics={analytics}/>
          </Route>
          <Route path="/segment-test-app/">
            <Home analytics={analytics}/>
          </Route>
        </Switch>
        </Router>
        </div>
    </div>
    );
  }

export default App;
