import React, { useEffect } from 'react'


function People(props) {

    props.analytics.page("People")


    function trackVoice() {
        props.analytics.track('Voice Followed', {
            voice: 'Joe Rogan',
        })
    }

    return (
        <div>
            <h1>People!</h1>
            <div>
                <button onClick={trackVoice}>
                    Follow Joe Rogan
                </button>
            </div>
        </div>
    )
}

export default People
